'use client';

import { useState, useEffect } from 'react';
import { Plus, Edit, Trash2, X, Save, Image as ImageIcon, FolderOpen, ChevronRight } from 'lucide-react';

type Album = {
  id: number;
  titulo: string;
  descripcion: string;
  imagen_portada: string;
  fecha: string;
  activo: boolean;
};

type GalleryImage = {
  id: number;
  album_id: number;
  titulo: string;
  descripcion: string;
  imagen_url: string;
  orden: number;
};

const emptyAlbum: Omit<Album, 'id'> = {
  titulo: '',
  descripcion: '',
  imagen_portada: '',
  fecha: new Date().toISOString().split('T')[0],
  activo: true,
};

const emptyImage: Omit<GalleryImage, 'id'> = {
  album_id: 0,
  titulo: '',
  descripcion: '',
  imagen_url: '',
  orden: 0,
};

export default function GalleryPage() {
  const [albums, setAlbums] = useState<Album[]>([]);
  const [images, setImages] = useState<GalleryImage[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [selectedAlbum, setSelectedAlbum] = useState<Album | null>(null);
  const [isAlbumModalOpen, setIsAlbumModalOpen] = useState(false);
  const [isImageModalOpen, setIsImageModalOpen] = useState(false);
  const [editingAlbum, setEditingAlbum] = useState<Album | null>(null);
  const [editingImage, setEditingImage] = useState<GalleryImage | null>(null);
  const [albumFormData, setAlbumFormData] = useState(emptyAlbum);
  const [imageFormData, setImageFormData] = useState(emptyImage);
  const [isSaving, setIsSaving] = useState(false);

  useEffect(() => {
    fetchAlbums();
  }, []);

  useEffect(() => {
    if (selectedAlbum) {
      fetchImages(selectedAlbum.id);
    }
  }, [selectedAlbum]);

  const fetchAlbums = async () => {
    try {
      const response = await fetch('/api/admin/gallery/albums');
      const data = await response.json();
      if (data.success) {
        setAlbums(data.data);
      }
    } catch (error) {
      console.error('Error al cargar álbumes:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const fetchImages = async (albumId: number) => {
    try {
      const response = await fetch(`/api/admin/gallery/images?albumId=${albumId}`);
      const data = await response.json();
      if (data.success) {
        setImages(data.data);
      }
    } catch (error) {
      console.error('Error al cargar imágenes:', error);
    }
  };

  const openCreateAlbumModal = () => {
    setEditingAlbum(null);
    setAlbumFormData(emptyAlbum);
    setIsAlbumModalOpen(true);
  };

  const openEditAlbumModal = (album: Album) => {
    setEditingAlbum(album);
    setAlbumFormData({
      titulo: album.titulo,
      descripcion: album.descripcion,
      imagen_portada: album.imagen_portada,
      fecha: album.fecha,
      activo: album.activo,
    });
    setIsAlbumModalOpen(true);
  };

  const openCreateImageModal = () => {
    if (!selectedAlbum) return;
    setEditingImage(null);
    setImageFormData({ ...emptyImage, album_id: selectedAlbum.id, orden: images.length + 1 });
    setIsImageModalOpen(true);
  };

  const openEditImageModal = (image: GalleryImage) => {
    setEditingImage(image);
    setImageFormData({
      album_id: image.album_id,
      titulo: image.titulo,
      descripcion: image.descripcion,
      imagen_url: image.imagen_url,
      orden: image.orden,
    });
    setIsImageModalOpen(true);
  };

  const handleAlbumSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSaving(true);

    try {
      const url = editingAlbum ? `/api/admin/gallery/albums/${editingAlbum.id}` : '/api/admin/gallery/albums';
      const response = await fetch(url, {
        method: editingAlbum ? 'PUT' : 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(albumFormData),
      });

      if (response.ok) {
        fetchAlbums();
        setIsAlbumModalOpen(false);
      } else {
        const error = await response.json();
        alert(error.message || 'Error al guardar');
      }
    } catch (error) {
      console.error('Error:', error);
      alert('Error al guardar');
    } finally {
      setIsSaving(false);
    }
  };

  const handleImageSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSaving(true);

    try {
      const url = editingImage ? `/api/admin/gallery/images/${editingImage.id}` : '/api/admin/gallery/images';
      const response = await fetch(url, {
        method: editingImage ? 'PUT' : 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(imageFormData),
      });

      if (response.ok) {
        fetchImages(selectedAlbum!.id);
        setIsImageModalOpen(false);
      } else {
        const error = await response.json();
        alert(error.message || 'Error al guardar');
      }
    } catch (error) {
      console.error('Error:', error);
      alert('Error al guardar');
    } finally {
      setIsSaving(false);
    }
  };

  const deleteAlbum = async (id: number) => {
    if (!confirm('¿Estás seguro de eliminar este álbum y todas sus imágenes?')) return;

    try {
      const response = await fetch(`/api/admin/gallery/albums/${id}`, { method: 'DELETE' });
      if (response.ok) {
        setAlbums(albums.filter(a => a.id !== id));
        if (selectedAlbum?.id === id) {
          setSelectedAlbum(null);
          setImages([]);
        }
      }
    } catch (error) {
      console.error('Error:', error);
    }
  };

  const deleteImage = async (id: number) => {
    if (!confirm('¿Estás seguro de eliminar esta imagen?')) return;

    try {
      const response = await fetch(`/api/admin/gallery/images/${id}`, { method: 'DELETE' });
      if (response.ok) {
        setImages(images.filter(i => i.id !== id));
      }
    } catch (error) {
      console.error('Error:', error);
    }
  };

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-[60vh]">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
          <p className="mt-4 text-gray-600">Cargando galería...</p>
        </div>
      </div>
    );
  }

  return (
    <div>
      <div className="bg-white rounded-lg shadow-sm p-6 mb-6">
        <div className="flex items-center justify-between">
          <div className="flex items-center gap-2">
            {selectedAlbum && (
              <>
                <button onClick={() => { setSelectedAlbum(null); setImages([]); }} className="text-blue-600 hover:text-blue-800">
                  Álbumes
                </button>
                <ChevronRight className="w-4 h-4 text-gray-400" />
                <span className="font-medium text-gray-900">{selectedAlbum.titulo}</span>
              </>
            )}
            {!selectedAlbum && (
              <div>
                <h1 className="text-3xl font-bold text-gray-900">Galería</h1>
                <p className="text-gray-600 mt-1">Gestiona los álbumes e imágenes</p>
              </div>
            )}
          </div>
          <button onClick={selectedAlbum ? openCreateImageModal : openCreateAlbumModal} className="flex items-center gap-2 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors">
            <Plus className="w-5 h-5" />
            {selectedAlbum ? 'Agregar Imagen' : 'Crear Álbum'}
          </button>
        </div>
      </div>

      {!selectedAlbum ? (
        // Albums Grid
        albums.length > 0 ? (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {albums.map((album) => (
              <div key={album.id} className={`bg-white rounded-lg shadow-sm overflow-hidden ${!album.activo ? 'opacity-60' : ''}`}>
                <div className="relative aspect-video bg-gray-100 cursor-pointer" onClick={() => setSelectedAlbum(album)}>
                  {album.imagen_portada ? (
                    <img src={album.imagen_portada} alt={album.titulo} className="w-full h-full object-cover" />
                  ) : (
                    <div className="w-full h-full flex items-center justify-center">
                      <FolderOpen className="w-16 h-16 text-gray-400" />
                    </div>
                  )}
                  <div className="absolute inset-0 bg-black bg-opacity-0 hover:bg-opacity-20 transition-all flex items-center justify-center">
                    <span className="text-white opacity-0 hover:opacity-100 font-medium">Ver imágenes</span>
                  </div>
                </div>
                
                <div className="p-4">
                  <h3 className="font-semibold text-gray-900">{album.titulo}</h3>
                  {album.descripcion && <p className="text-sm text-gray-600 mt-1 line-clamp-2">{album.descripcion}</p>}
                  <p className="text-xs text-gray-400 mt-2">{new Date(album.fecha).toLocaleDateString('es-PE')}</p>
                </div>
                
                <div className="px-4 pb-4 flex items-center justify-end gap-2">
                  <button onClick={() => openEditAlbumModal(album)} className="p-2 text-blue-600 hover:bg-blue-50 rounded-lg transition-colors" title="Editar">
                    <Edit className="w-5 h-5" />
                  </button>
                  <button onClick={() => deleteAlbum(album.id)} className="p-2 text-red-600 hover:bg-red-50 rounded-lg transition-colors" title="Eliminar">
                    <Trash2 className="w-5 h-5" />
                  </button>
                </div>
              </div>
            ))}
          </div>
        ) : (
          <div className="bg-white rounded-lg shadow-sm p-12 text-center">
            <FolderOpen className="w-16 h-16 text-gray-400 mx-auto mb-4" />
            <h3 className="text-lg font-semibold text-gray-900 mb-2">No hay álbumes</h3>
            <p className="text-gray-600 mb-4">Crea el primer álbum de la galería</p>
            <button onClick={openCreateAlbumModal} className="inline-flex items-center gap-2 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700">
              <Plus className="w-5 h-5" />
              Crear Álbum
            </button>
          </div>
        )
      ) : (
        // Images Grid
        images.length > 0 ? (
          <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
            {images.map((image) => (
              <div key={image.id} className="bg-white rounded-lg shadow-sm overflow-hidden group">
                <div className="relative aspect-square bg-gray-100">
                  <img src={image.imagen_url} alt={image.titulo} className="w-full h-full object-cover" />
                  <div className="absolute inset-0 bg-black bg-opacity-0 group-hover:bg-opacity-40 transition-all flex items-center justify-center gap-2 opacity-0 group-hover:opacity-100">
                    <button onClick={() => openEditImageModal(image)} className="p-2 bg-white rounded-full text-blue-600 hover:bg-blue-50">
                      <Edit className="w-4 h-4" />
                    </button>
                    <button onClick={() => deleteImage(image.id)} className="p-2 bg-white rounded-full text-red-600 hover:bg-red-50">
                      <Trash2 className="w-4 h-4" />
                    </button>
                  </div>
                </div>
                {image.titulo && <p className="p-2 text-sm text-gray-600 truncate">{image.titulo}</p>}
              </div>
            ))}
          </div>
        ) : (
          <div className="bg-white rounded-lg shadow-sm p-12 text-center">
            <ImageIcon className="w-16 h-16 text-gray-400 mx-auto mb-4" />
            <h3 className="text-lg font-semibold text-gray-900 mb-2">No hay imágenes</h3>
            <p className="text-gray-600 mb-4">Agrega la primera imagen al álbum</p>
            <button onClick={openCreateImageModal} className="inline-flex items-center gap-2 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700">
              <Plus className="w-5 h-5" />
              Agregar Imagen
            </button>
          </div>
        )
      )}

      {/* Album Modal */}
      {isAlbumModalOpen && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-lg max-w-lg w-full">
            <div className="p-6 border-b border-gray-200">
              <div className="flex items-center justify-between">
                <h2 className="text-xl font-bold text-gray-900">{editingAlbum ? 'Editar Álbum' : 'Crear Álbum'}</h2>
                <button onClick={() => setIsAlbumModalOpen(false)} className="p-2 text-gray-400 hover:text-gray-600 transition-colors">
                  <X className="w-6 h-6" />
                </button>
              </div>
            </div>

            <form onSubmit={handleAlbumSubmit}>
              <div className="p-6 space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Título *</label>
                  <input type="text" value={albumFormData.titulo} onChange={(e) => setAlbumFormData({ ...albumFormData, titulo: e.target.value })} required className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent" />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Descripción</label>
                  <textarea value={albumFormData.descripcion} onChange={(e) => setAlbumFormData({ ...albumFormData, descripcion: e.target.value })} rows={2} className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent" />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">URL Imagen Portada</label>
                  <input type="text" value={albumFormData.imagen_portada} onChange={(e) => setAlbumFormData({ ...albumFormData, imagen_portada: e.target.value })} className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent" />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Fecha</label>
                  <input type="date" value={albumFormData.fecha} onChange={(e) => setAlbumFormData({ ...albumFormData, fecha: e.target.value })} className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent" />
                </div>
                <label className="flex items-center gap-2 cursor-pointer">
                  <input type="checkbox" checked={albumFormData.activo} onChange={(e) => setAlbumFormData({ ...albumFormData, activo: e.target.checked })} className="w-5 h-5 rounded border-gray-300 text-blue-600 focus:ring-blue-500" />
                  <span className="text-sm font-medium text-gray-700">Álbum activo</span>
                </label>
              </div>

              <div className="p-6 border-t border-gray-200 bg-gray-50 flex justify-end gap-3">
                <button type="button" onClick={() => setIsAlbumModalOpen(false)} className="px-4 py-2 text-gray-700 bg-white border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors">Cancelar</button>
                <button type="submit" disabled={isSaving} className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors disabled:opacity-50 flex items-center gap-2">
                  {isSaving ? <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin"></div> : <Save className="w-4 h-4" />}
                  {editingAlbum ? 'Actualizar' : 'Crear'}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}

      {/* Image Modal */}
      {isImageModalOpen && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-lg max-w-lg w-full">
            <div className="p-6 border-b border-gray-200">
              <div className="flex items-center justify-between">
                <h2 className="text-xl font-bold text-gray-900">{editingImage ? 'Editar Imagen' : 'Agregar Imagen'}</h2>
                <button onClick={() => setIsImageModalOpen(false)} className="p-2 text-gray-400 hover:text-gray-600 transition-colors">
                  <X className="w-6 h-6" />
                </button>
              </div>
            </div>

            <form onSubmit={handleImageSubmit}>
              <div className="p-6 space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">URL de Imagen *</label>
                  <input type="text" value={imageFormData.imagen_url} onChange={(e) => setImageFormData({ ...imageFormData, imagen_url: e.target.value })} required className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent" placeholder="/images/gallery/foto.jpg" />
                  {imageFormData.imagen_url && (
                    <div className="mt-2 aspect-video bg-gray-100 rounded-lg overflow-hidden">
                      <img src={imageFormData.imagen_url} alt="Preview" className="w-full h-full object-cover" onError={(e) => { (e.target as HTMLImageElement).style.display = 'none'; }} />
                    </div>
                  )}
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Título</label>
                  <input type="text" value={imageFormData.titulo} onChange={(e) => setImageFormData({ ...imageFormData, titulo: e.target.value })} className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent" />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Descripción</label>
                  <textarea value={imageFormData.descripcion} onChange={(e) => setImageFormData({ ...imageFormData, descripcion: e.target.value })} rows={2} className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent" />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Orden</label>
                  <input type="number" value={imageFormData.orden} onChange={(e) => setImageFormData({ ...imageFormData, orden: parseInt(e.target.value) })} min="1" className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent" />
                </div>
              </div>

              <div className="p-6 border-t border-gray-200 bg-gray-50 flex justify-end gap-3">
                <button type="button" onClick={() => setIsImageModalOpen(false)} className="px-4 py-2 text-gray-700 bg-white border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors">Cancelar</button>
                <button type="submit" disabled={isSaving} className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors disabled:opacity-50 flex items-center gap-2">
                  {isSaving ? <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin"></div> : <Save className="w-4 h-4" />}
                  {editingImage ? 'Actualizar' : 'Agregar'}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
}
